import "./App.css";
import { message, Button, Col, Input, Row, Typography, InputNumber, DatePicker, Radio, Space } from "antd";
import { map, pipe, toArray, toAsync } from "@fxts/core";
import { useEffect, useState } from "react";
import { addMonths, format, parse } from "date-fns";

const { Text } = Typography;

interface WhooingData {
  entry_date: string;
  item: string;
  money: number;
  left: string;
  right: string;
  memo: string;
}

type CheckMode = "CLIPBOARD" | "TEMP" | "INPUT";

type CalculateMode = "SHINHAN" | "SHINHAN";

function App() {
  const [postUrl, setPostUrl] = useState("");
  const [priceStr, setPriceStr] = useState("");
  const [isurlValid, setIsUrlValid] = useState(false);
  const [price, setPrice] = useState(0);
  const [month, setMonth] = useState(2);
  const [itemName, setItemName] = useState("");
  const [left, setLeft] = useState("");
  const [right, setRight] = useState("");
  const [memo, setMemo] = useState("");
  const [date, setDate] = useState<string | null>(null);

  const [calculateMethod, setCalculateMethod] = useState<CalculateMode>("SHINHAN");

  // const [debugStr, setDebugStr] = useState("");
  const [canContinueClipboard, setCanContinueClipboard] = useState(false);
  const [canContinueTemp, setCanContinueTemp] = useState(false);
  const [canContinueInput, setCanContinueInput] = useState(false);
  useEffect(() => {
    setCanContinueClipboard(getData("CLIPBOARD", true, true) === null ? false : true);
    setCanContinueTemp(getData("TEMP", true, true) === null ? false : true);
    setCanContinueInput(getData("INPUT", true, true) === null ? false : true);

    // const a = getData("CLIPBOARD", true);
    // if (a) {
    //   setDebugStr(a);
    // }
  }, [postUrl, priceStr, isurlValid, price, month, itemName, left, right, memo, date]);

  useEffect(() => {
    if (/\?/.test(postUrl)) {
      setIsUrlValid(false);
    } else if (/^https:\/\/whooing.com\/webhook\/s\//.test(postUrl)) {
      setIsUrlValid(true);
    } else {
      setIsUrlValid(false);
    }
  }, [postUrl]);

  useEffect(() => {
    const value = parseInt(priceStr.replace(/[^0-9]/g, ""));
    if (!isNaN(value)) {
      setPrice(value);
    } else {
      setPrice(0);
    }
  }, [priceStr]);

  function getData(checkMode: "CLIPBOARD", skipError?: boolean, onlyCheck?: boolean): string | null;
  function getData(checkMode: "TEMP", skipError?: boolean, onlyCheck?: boolean): string[] | null;
  function getData(checkMode: "INPUT", skipError?: boolean, onlyCheck?: boolean): WhooingData[] | null;
  function getData(
    checkMode: CheckMode,
    skipError: boolean = false,
    onlyCheck: boolean = false,
  ): string | string[] | WhooingData[] | null {
    const err = (msg: string) => (skipError ? undefined /*console.log(msg)*/ : message.error(msg));

    if (checkMode !== "CLIPBOARD") {
      if (!isurlValid) {
        err("POST URL .");
        return null;
      }
    }

    if (price <= 0 || price > 1000000000) {
      err(" .");
      return null;
    }

    if (!date) {
      err(" .");
      return null;
    }

    if (isNaN(month) || month <= 1) {
      err("  .");
      return null;
    }

    if (itemName.length === 0) {
      err(" .");
      return null;
    }

    if (left.length === 0) {
      err("   .");
      return null;
    }

    if (right.length === 0) {
      err("   .");
      return null;
    }

    if (onlyCheck) {
      return "";
    }

    const pricePerMonth = price / month;
    const a = Math.floor(pricePerMonth / 100) * 100;
    const b = price - a * (month - 1);
    const arr = new Array<number>(month).fill(a);
    arr[0] = b;

    let item = itemName.trim();
    if (/\(/.test(item)) {
      item = `${item.replace(/\)$/, "")}, `;
    } else {
      item = `${item}(`;
    }

    const leftString = left.trim();
    const rightString = right.trim();
    const memoString = memo.trim();

    if (checkMode !== "INPUT") {
      const data = arr.map((price, i) => {
        const nextDate = parse(date, "yyyyMMdd", new Date());
        const dateString = format(addMonths(nextDate, i), "yyyyMMdd");
        return [dateString, `${item}${i + 1}/${month})`, price, leftString, rightString, memoString]
          .filter((v) => v !== "")
          .join(", ");
      });

      if (checkMode === "TEMP") {
        return data;
      }
      return data.join("\n");
    }
    const data = arr.map<WhooingData>((price, i) => {
      const nextDate = parse(date, "yyyyMMdd", new Date());
      const dateString = format(addMonths(nextDate, i), "yyyyMMdd");
      return {
        entry_date: dateString,
        item: `${item}${i + 1}/${month})`,
        money: price,
        left: leftString,
        right: rightString,
        memo: memoString,
      };
      // return [dateString, `${item}${i + 1}/${month})`, price, leftString, rightString, memoString];
    });

    return data;
  }

  return (
    <div className="App">
      <h1>  </h1>
      <h2>   .</h2>
      <h2>     .</h2>
      <br />
      <br />
      <br />
      <br />

      <h3> </h3>
      <h4>
          
        <a href="https://github.com/Kuhave/whooing-card-installment/issues"> </a>  {" "}
        <Text code>kuhave@gmail.com</Text>       .
      </h4>
      <Radio.Group
        onChange={(e) => {
          setCalculateMethod(e.target.value);
        }}
        value={calculateMethod}
      >
        <Space direction="vertical">
          <Radio value={"SHINHAN"}>, , </Radio>
        </Space>
      </Radio.Group>
      <br />
      <br />
      <br />

      <h3> </h3>
      <Input
        size="large"
        status={price <= 0 || price > 1000000000 ? "error" : undefined}
        prefix=""
        placeholder="( )"
        value={priceStr}
        onChange={(e) => {
          const value = parseInt(e.target.value.replace(/[^0-9]/g, ""));
          if (isNaN(value)) {
            setPriceStr("");
            return;
          } else if (value > 1000000000) {
            setPriceStr("1000000000");
          } else {
            setPriceStr(value.toLocaleString());
          }
        }}
      />
      <br />
      <br />
      <br />

      <h3> </h3>
      <DatePicker
        size="large"
        status={!date ? "error" : undefined}
        placeholder=" "
        onChange={(e) => {
          if (e) {
            setDate(format(e.toDate(), "yyyyMMdd"));
          }
        }}
      />
      <br />
      <br />
      <br />

      <h3>  </h3>
      <InputNumber
        size="large"
        status={isNaN(month) || month <= 1 ? "error" : undefined}
        prefix=" : "
        value={month}
        onChange={(e) => {
          setMonth(e);
        }}
      />
      <br />
      <br />
      <br />

      <h3> </h3>
      <h4>   ,    .(     )</h4>
      <Input
        size="large"
        placeholder=" ."
        status={itemName.length === 0 ? "error" : undefined}
        value={itemName}
        onChange={(e) => {
          setItemName(e.target.value);
        }}
      />
      <br />
      <br />
      <br />

      <h3>/  (    )</h3>
      <Row>
        <Col span={12}>
          <h3></h3>
          <Input
            size="large"
            placeholder=""
            status={left.length === 0 ? "error" : undefined}
            width={12}
            value={left}
            onChange={(e) => {
              setLeft(e.target.value);
            }}
          />
        </Col>
        <Col span={12}>
          <h3></h3>
          <Input
            size="large"
            placeholder=""
            status={right.length === 0 ? "error" : undefined}
            width={12}
            value={right}
            onChange={(e) => {
              setRight(e.target.value);
            }}
          />
        </Col>
      </Row>

      <br />
      <br />
      <br />

      <h3>()</h3>
      <Input
        size="large"
        placeholder=" ."
        value={memo}
        onChange={(e) => {
          setMemo(e.target.value);
        }}
      />
      <br />
      <br />
      <br />
      <br />

      <h3>
        {"POST URL (   .)"}
        <br />
        <a href="https://whooing.com/#main/setting">https://whooing.com/#main/setting</a>
        <>
          &nbsp; <Text code>POST URL (  )</Text> URL   .
        </>
      </h3>
      <Input
        size="large"
        status={isurlValid ? undefined : "warning"}
        placeholder="POST URL"
        width={12}
        value={postUrl}
        onChange={(e) => {
          setPostUrl(e.target.value);
        }}
      />
      <br />
      <br />
      <br />

      <Button
        block
        disabled={!canContinueClipboard}
        type="default"
        size="large"
        onClick={() => {
          const data = getData("CLIPBOARD");
          if (data) {
            navigator.clipboard.writeText(data);
            message.info(
              <>
                <br />
                   .
                <br />
                <a href="https://whooing.com/#main/insert"></a>  -   .
              </>,
              3,
            );
          }
        }}
      >
           (    .)
      </Button>
      <br />
      <br />
      <Button
        block
        disabled={!canContinueTemp}
        type="primary"
        size="large"
        onClick={async () => {
          const data = getData("TEMP");
          if (data) {
            await pipe(
              data,
              toAsync,
              map(async (v) => {
                const result = await fetch(postUrl, {
                  method: "POST",
                  headers: { "Content-Type": "application/json" },
                  body: JSON.stringify({ message: v }),
                })
                  .then((res) => res.text())
                  .then((res) => res === "done")
                  .catch((e) => {
                    console.log(e);
                    return null;
                  });

                return result;
              }),
              toArray,
              (v) => {
                if (v.filter((v) => typeof v !== "boolean").length > 0) {
                  message.error(`  .`);
                } else {
                  message.info(`${v.length}    .`);
                }
              },
            );
          }
        }}
      >
          (    .)
      </Button>
      <br />
      <br />
      <Button
        block
        disabled={!canContinueInput}
        type="primary"
        size="large"
        onClick={async () => {
          const data = getData("INPUT");
          if (data) {
            await pipe(
              data,
              toAsync,
              map(async (v) => {
                const result = await fetch(postUrl, {
                  method: "POST",
                  headers: { "Content-Type": "application/json" },
                  body: JSON.stringify(v),
                })
                  .then((res) => res.text())
                  .then((res) => res === "done")
                  .catch((e) => {
                    console.log(e);
                    return null;
                  });

                return result;
              }),
              toArray,
              (v) => {
                if (v.filter((v) => typeof v !== "boolean").length > 0) {
                  message.error(`  .`);
                } else {
                  message.info(`${v.length}   .`);
                }
              },
            );
          }
        }}
      >
          
      </Button>
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <h2 style={{ textAlign: "center" }}>
        <a href="https://github.com/Kuhave/whooing-card-installment">created by Kuhave @ 2022</a>
      </h2>
    </div>
  );
}

export default App;
